body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  background-color: #29a4a8;
}

.logo {
  width: 50px;
  height: 50px;
  margin: 15px;
}
.App {
  height: 100vh;
}
.Main {
  
  background-size: cover;
  height: calc(100vh - 80px);
  position: absolute;
  width: 100%;
  transition: 0.5s;
}

.small {
  height: 30vh;
  background: url(/static/media/plane-841441_1920.33bf7415.jpg) top left;
  background-size: cover;
}

.right {
  float: right;
}

.padd {
  padding: 20px;
}

.seperator {
  margin: 0px auto;
  margin-top: 80px;
  padding: 5px 16px;
  width: 80%;
  border-radius: 8px;
  background: #000;
  color: #fff;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
}

.message-container {
  border-radius: 8px;
  background: #fff;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
  margin: 40px auto;
  padding: 20px;
  text-align: center;
  font-size: 20px;
  width: 80%;
}

.price-slider {
  margin: 20px 0px;
  padding: 10px;
  width: 90%;
  display: -webkit-flex;
  display: flex;
}

.symbol {
  font-size: 36px;
  padding: 0px 20px;
  margin-top: -10px;
}

.input-range__slider {
  background: #29a4a8;
  border: 1px solid #29a4a8 ;
}

.input-range__label {
  color: #fff
}
.input-range__track {
  background: #fff;
}

.input-range__track--active {
  background: #29a4a8;
}

@media (max-width: 767px) {
  .right {
    float: none;
  }
  .price-slider {
    width: 85%;
  }
}
.button {
  background-color: rgb(0, 0, 0);
  border-radius: 5px;
  color: #29a4a8;
  cursor: pointer;
  padding: 10px 15px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.container {
  margin-right: 5px;
  width: 24%;
}

.label {
  color: #808080;
}

.calendar-container {
  position: absolute;
  z-index: 1000;
}

.hide {
  display: none;
}
.container {
  margin-right: 5px;
  margin-bottom: 10px;
  width: 24%;
}

.label {
  color: #808080;
}

.input {
  border-radius: 3px;
  border: 1px solid #e5e5e5;
  font-size: 16px;
  padding: 10px 15px;
  padding-left: 32px;
  width: -webkit-fill-available;
}

.input:focus {
  outline: none;
  border: 1px solid #fed538;
}

.icon {
  position: absolute;
  padding: 5px 0px;
}

.list-container {
  background-color: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
  max-height: 200px;
  overflow: auto;
  position: absolute;
  width: inherit;
  z-index: 100;
}

.list {
  list-style: none;
  padding: 0px;
  margin: 0px;
}

.list-item {
  padding: 10px 15px;
  border-bottom: .5px solid #e5e5e5;
}

.hide {
  display: none;
}

@media (max-width: 767px) {
  .container {
    width: 100%
  }
}
.tag {
  color: #000;
  border: 1.5px black solid;
  border-radius: 20px;
  cursor: pointer;
  margin-right: 10px;
  padding: 5px 10px;
}

.active {
  color: #29a4a8;
  background-color: #000;
}

.card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
  margin: 0 auto;
  padding: 8px 16px 48px;
  position: relative;
  top: 25vh;
  transition: 0.5s;
  width: 80%;
}

.top {
  top: 50px;
}

.flexed, .form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

@media (max-width: 767px) {
  .card {
    top: 10vh;
  }
  .top {
    top: 50px;
  }
  .form {
    display: block;
  }
}
.flight-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
  margin: 10px auto;
  padding: 8px 16px 48px;
  transition: 0.5s;
  width: 80%;
}

.price {
  float: right;
  font-size: 26px;
}

.book-button {
  float: right;
}

.detail-container {
  display: -webkit-flex;
  display: flex;
}

.id {
  padding: 5px 15px;
}

.inner {
  padding: 5px 25px;
}

.time {
  padding-bottom: 5px;
}

.duration {
  font-size: 22px;
}

.city {
  color: #3c4043
}

.green {
  color: green;
}

.red {
  color: red;
}

@media (max-width: 767px) {
  .price {
    float: none;
    margin-bottom: 5px;
  }
  .id {
    padding: 5px;
  }
  .inner {
    padding: 5px;
  }
}
